<template>
  <div class="content">
    <div class="container-fluid">

      <div class="containers">
        <h3>container list</h3>
        <div v-for="container in containers" v-bind:key="container.id" class="container">
          <div>
            <strong>{{container.name}}</strong>, <i>{{container.language}}</i> [{{container.maximumstudents}}]<i>{{container.explanation}}</i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    created () {
      this.$http.get('/api/containers')
      .then((response) => {
        this.containers = response.data
      })
    },
    data () {
      return {
        containers: []
      }
    }
  }
</script>
<style scoped>

.container {
  display: inline-block;
  padding: 10px;
  text-align: left;
}
</style>



